import { LeftOutlined, LogoutOutlined } from "@ant-design/icons";
import { Button } from "antd";
import axios from "axios";
import { useLocation, useNavigate } from "react-router-dom";
import './Top.css';
type Props = {
  title: string,
  next?: boolean,
  logout?: boolean,
  color?: string,
}
export default function Top(props:Props) {
  const { title, next = false, logout = false, color = ''} = props
  const navigate = useNavigate()
  const myLocation = useLocation()
  function onNext() {
    window.history.back()
  }
  function onLogout() {
    if(window.confirm('')) {
      axios.get('/account/logout').then(() => {
        navigate(myLocation.pathname)
      }).catch(reject => {
        alert('')
      })
    }
  }
  return (
    <div style={{'backgroundColor': color }} className="Top">
      <div className="Top-left">
        { next &&
        <div>
          <LeftOutlined onClick={onNext} />
        </div>
        }
        <div>{title}</div>
      </div>
      <div className="Top-right">
        { logout &&
          <Button shape='round'  className="Top-logout" onClick={onLogout}>
            <LogoutOutlined />
            
          </Button>
        }
      </div>
    </div>
  )
}
