import { Link, NavLink, Outlet } from "react-router-dom";
import Top from "../../../../components/Top/Top";
import './SelectCreation.css';
import SinglePng from '@/assets/single.png';
import MultiplePng from '@/assets/multiple.png';
import { Button } from "antd";
export default function SelectCreation() {

  return (
    <div className="SelectCreation">
      <Top title={""} color="#F3F4F6"></Top>
      <div className="SelectCreation-choose">
        <div className="single">
          <img src={SinglePng} alt="" />
          <Button type="primary" >
            <Link to="/create?type=single" >
              
            </Link>
          </Button>
        </div>
        <div className="multiple">
          <img src={MultiplePng} alt="" />
          <Button type="primary" >
            <Link to="/create?type=multiple" >
              
            </Link>
          </Button>
        </div>
      </div>
    </div>
  )
}
