import { Navigate, Route, Routes } from "react-router-dom";
import Login from "./pages/Login/Login";
import Create from "./pages/CreateVote/CreateVote";
import ViewVote from "./pages/ViewVote/ViewVote";

import Home from "./pages/Home/Home";
import Me from "./pages/Home/components/Me/Me";
import SelectCreation from "./pages/Home/components/SelectCreation/SelectCreation";
import LoginCheck from "./components/LoginCheck";
import React, { ReactNode } from "react";
import Register from "./pages/Register/Register";



export default function Vote() {

  return(
    <Routes>
      <Route path="/" element={<Navigate replace to="home" />}/>
      <Route path="/home" element={<Home />}>
        <Route path="" element={<Navigate replace to="create" />}/>
        <Route path="create" element={<SelectCreation/>} />

        <Route path="me" element={(<Me/>)}/>
      </Route>
      <Route path="/create" element={<Create />} />
      <Route path="/vote/:id" element={<ViewVote />}/>
      <Route path="/login" element={<Login />}></Route>
      <Route path="/register" element={<Register/>}></Route>
    </Routes>

  )
}
