import { FileAddOutlined, UserOutlined } from "@ant-design/icons";
import { NavLink, Outlet } from "react-router-dom";
import './Home.css';

export default function Home() {

  return (
    <div className="Home">
      <Outlet></Outlet>
      <br />
      <div className="Bottom">
        <div className="Bottom-left">
          <NavLink  className={'Bottom-item'} style={({isActive}) => {
            return isActive? {color: '#1890ff'}: {color: '#8E8E8E'}
          }} to="create">
            <FileAddOutlined />
            <span></span>
          </NavLink>
        </div>
        <div className="Bottom-right">
          <NavLink className={'Bottom-item'} style={({isActive}) => {
            return isActive? {color: '#1890ff'}: {color: '#8E8E8E'}
          }} to="me">
            <UserOutlined  />
            <span></span>
          </NavLink>
        </div>
      </div>
    </div>
  )
}
