import { ChangeEvent, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import {useInput} from "rooks"
import { useImmer } from "use-immer";
import { MinusCircleFilled,
        PlusCircleFilled,} from '@ant-design/icons';
import {Button, DatePicker, Form, Input, Switch, TimePicker, } from 'antd';

import './CreateVote.css';
import moment from "moment";
import axios from "axios";
import Top from "../../components/Top/Top";
import LoginCheck from "../../components/LoginCheck";
let map = {
  single: '',
  multiple: '',
}

type VoteType = keyof typeof map

function CreateVote() {

  let [search, setSearch] = useSearchParams()
  let type = search.get('type') as VoteType
  console.log(type)
  const navigate = useNavigate()
  const titleInput = useInput('')
  const descInput = useInput('')
  const [options, setOptions] = useImmer<string[]>(['', ''])
  const [date, setDate] = useImmer<string[]>([...(moment(new Date()).add(1, 'y').toISOString().split('T'))])
  const [anonymous, setAnonymous] = useState(false)

  function onAnonymous(checked: boolean) {
    setAnonymous(checked)
  }
  console.log(date)

  function addOption() {
    setOptions(options => {
      options.push('')
    })
  }
  function updateOptions(e: ChangeEvent<HTMLInputElement>, idx: number) {
    setOptions(options => {
      options[idx] = e.target.value
    })
  }
  function delOption(idx: number) {
    setOptions(options => {
      options.splice(idx, 1)
    })
  }

  function changeDate({i, m}: {i:number, m: moment.Moment | null}) {
    m?.add(1, 'd')
    console.log(m?.toString(), i)
    console.log(m, 'start', m?.toString())
    setDate(date => {
      date[i] = m?.toISOString()?.split('T')[i] ?? ''
      console.log(date,'end' )
    })
  }
  function onFinish(w: any) {
    console.log(w, 'onfinsh')
  }
  async function onSubmit(w: any) {
    const Voteinfo = {
      title: titleInput.value,
      desc: descInput.value,
      deadline: date.join('T'),
      anonymous: anonymous,
      multiple: type == 'multiple',
      options: options,
    }
    if(Voteinfo.title && (Voteinfo.options.findIndex(it => it == '') < 0)) {
      const res = await axios.post('/vote', Voteinfo)
      const voteId = res.data.result.voteId
      navigate('/vote/'+voteId)
      console.log(Voteinfo)
    } else {
      alert('')
    }
  }
  const [d, setD] = useState(new Date().toISOString())
  return (
    <div className="Create-Vote">
      <Top title={`${map[type]}`} color="#fff" next={true}></Top>
      <Form onFinish={onFinish} >
        <div className="Create-Vote-top">
          <Form.Item className="title" >
            <Input required={true} size="large" bordered={false} placeholder="" {...titleInput}/>
          </Form.Item>
          <Form.Item className="Create-Vote-devc" >
            <Input size="large" bordered={false} placeholder="()"  {...descInput}/>
          </Form.Item>
          {
            options.map((opt, idx) => {
              return (
                  <Form.Item key={idx} className="Option">

                    <Input
                      required={true}
                      size="large"
                      bordered={false} type="text"
                      prefix={<MinusCircleFilled onClick={() => delOption(idx)} className="Create-Vote-sub" />}
                      onChange={(e) => updateOptions(e, idx)}
                      value={opt}
                      placeholder=""/>
                  </Form.Item>
              )
            })
          }
          <Form.Item className="bordernone">
              <Button size="large"
                  icon={<PlusCircleFilled  className="Create-Vote-add" />}
                  type="link"
                  onClick={() => addOption()}></Button>
          </Form.Item>
        </div>

        <div className="Create-Vote-mid">
          <Form.Item>
            <div className="Create-Vote-Date">
              <span className="Create-Vote-Date-left"></span>
              <div className="Create-Vote-Date-right">
                <DatePicker
                  inputReadOnly={true}
                  className="date"
                  value={moment(date[0])}
                  onChange={(e) =>  changeDate({i: 0, m: e})}
                  allowClear={false}
                  suffixIcon=''
                  bordered={false}/>
                <TimePicker
                  inputReadOnly={true}
                  className="time"
                  suffixIcon=''
                  value={moment(date.join('T'))}
                  format='HH:mm'
                  onChange={(e) => changeDate({i: 1, m: e})}
                  allowClear={false}
                  bordered={false}/>
              </div>
            </div>
          </Form.Item>
          <Form.Item className='bordernone'>
            <div className="Create-Vote-Anonymous">
                <span className="left"></span>
                <div className="right">
                  <Switch checked={anonymous} onChange={onAnonymous}></Switch>
                </div>
            </div>
          </Form.Item>
        </div>

        <div className="Create-Vote-bottom">
          <Form.Item className="bordernone">
            <Button onClick={onSubmit} className="submit"  type="primary" block>
              
            </Button>
          </Form.Item>
        </div>

      </Form>
        {/* <div>
          <div>
            <input type="text" placeholder="" {...titleInput}/>
          </div>
          <div>
            <input type="text" placeholder="()" {...descInput}/>
          </div>
          <div>
            {
              options.map((opt, idx) => {
                return (
                  <div key={idx}>
                    <MinusCircleFilled onClick={() => delOption(idx)} className="Create-Vote-sub" />
                    <input type="text" onChange={(e) => updateOptions(e, idx)} value={opt} placeholder=""/>
                  </div>
                )
              })
            }
            <div>
              <label onClick={() => addOption()}>
                <PlusCircleFilled className="Create-Vote-add"  />
                <span ></span>
              </label>
            </div>
          </div>
          <div>
            <input value={date[0]} type="date" />
          </div>
          <div></div>

        </div> */}
    </div>
  )
}

export default LoginCheck(CreateVote, {blank: true})
