import { DeleteTwoTone, ProfileTwoTone } from "@ant-design/icons";
import { useRequest } from "ahooks";
import { Collapse } from "antd";
import axios from "axios";
import _ from "lodash";
import { useState } from "react";
import { Link } from "react-router-dom";
import LoginCheck from "../../../../components/LoginCheck";
import Top from "../../../../components/Top/Top";
import './Me.css';

const { Panel } =  Collapse


async function getVoteList() {
  const voteList = (await axios.get('/vote')).data.result
  const voteListP = Promise.all(
    voteList.map(async vote => {
      const voteId = vote.voteId
      const voteInfo = (await axios.get('/vote/' + voteId)).data.result
      //
      vote.voteUserCount = _.uniq(voteInfo.userVotes.map(it => it.userId)).length
      return vote
    }))
  return voteListP
}

function Me() {
  const {data: voteList, mutate, error, loading} = useRequest( getVoteList, {
    cacheKey: 'cacheKey-demo',
  } )
  const [foldId, setFoldId] = useState<number>()
  function onFolding(id) {
    if(foldId == id) {
      setFoldId(NaN)
    } else {
      setFoldId(id)
    }
  }
  function deleteVote( voteId, idx) {
    if(window.confirm('')) {
      mutate((voteList) => {
        voteList?.splice(idx, 1)
        return voteList
      })
      axios.delete('/vote/' + voteId).then(res => {
        console.log(res)
      }, error => {
        console.log(error)
      })
    }
  }
  //
  if(!voteList && loading) {

    return (<div>
              <Top title={""}></Top>
              <div>...</div>
            </div>)
  }
  return (
    <div className="Me">
      <Top title={""} logout={true}></Top>
      <Collapse ghost={true} className="Me-voteList"  expandIcon={()=>' '} accordion>
        {
          voteList?.map((vote, idx) => {
            const voteId = vote.voteId
            console.log(vote.voteUserCount)
            return (
                <Panel className="voteList-item" header={vote.title}
                  extra={ <span style={{color: 'rgb(187, 187, 187)'}}>{vote.voteUserCount}</span>}
                  key={voteId}>
                  <div className="item-content">
                    <Link className="view" to={'/vote/' + voteId}>
                      <ProfileTwoTone />
                      <span className="letter"></span>
                    </Link>
                    <div className="delete"  onClick={() => deleteVote(voteId, idx)}>
                      <DeleteTwoTone />
                      <span className="letter"></span>
                    </div>
                  </div>
                </Panel>
              )
            })
          }
      </Collapse>
      { loading &&
        <div>...</div>
      }
      <div className="voteList-bottom"></div>
    </div>
  )
}

export default LoginCheck(Me)
