import { useSetState } from "ahooks";
import { IProps } from "ahooks/lib/useWhyDidYouUpdate";
import axios from "axios";
import { reject, update } from "lodash";
import { resolve } from "path";
import { ReactNode, useEffect, useState } from "react";
import { Navigate, useLocation, useNavigate } from "react-router-dom";

interface options {
  blank?: boolean,
  nav?: string | undefined,
}

export default function LoginCheck(Comp,{blank, nav}:options = {blank: false, nav: undefined} ) {

  return function Check(ipops) {
    const [key, setKey] = useState<boolean | null>(null)
    const navigate = useNavigate()
    const myLocation = useLocation()
    axios.get('/account/current-user').then(() => {
      console.log(112)
      if(nav !== undefined) {
        navigate(nav, {replace: true})
        console.log('nav', nav)
      }
      setKey(true)
    }).catch(() => {
      if(myLocation.pathname != '/login') {
        sessionStorage.voteLastPage = myLocation.pathname
        navigate(`/login`)
        setKey(false)
      }
    })

    if(key || !blank) {
      return <Comp {...ipops}/>
    } else {
      return <div>...</div>
    }
  }
}
