import axios from "axios"
import { useNavigate } from "react-router-dom"
import Top from "../../components/Top/Top"
import { Button, Checkbox, Form, Input, Upload, UploadFile, UploadProps } from 'antd';
import React, { useState } from 'react';
import './Register.css';
import ImgCrop from "antd-img-crop";
import { RcFile } from "antd/lib/upload";

const Register: React.FC = () => {
  const Navigate = useNavigate()
  const [fileList, setFileList] = useState<UploadFile[]>([]);
  const [avatar, setAvatar] = useState('')

  const onUpload: UploadProps['onChange'] = ({file:file, fileList: newFileList }) => {
    if(file.status == 'done') {
      console.log(file)
      setAvatar(file.response[0])
      console.log(avatar)
    }
    setFileList(newFileList);
  };

  const onPreview = async (file: UploadFile) => {
    let src = file.url as string;
    if (!src) {
      src = await new Promise(resolve => {
        const reader = new FileReader();
        reader.readAsDataURL(file.originFileObj as RcFile);
        reader.onload = () => resolve(reader.result as string);
      });
    }
    const image = new Image();
    image.src = src;
    const imgWindow = window.open(src);
    imgWindow?.document.write(image.outerHTML);
  };


  function registerSubmit(registerInfo) {

    return axios.post('/account/register', registerInfo).then(()=> {
      console.log('j')
      Navigate('/login',{replace: true})
    }, ()=> alert('!'))
  }

  const onFinish = async (values: any) => {
    const registerInfo = {...values, avatar}
    const k = await registerSubmit(registerInfo)
    console.log('Success:', values, k);
  };

  const onFinishFailed = (errorInfo: any) => {
    alert('')
    console.log('Failed:', errorInfo);
  };

  return (
    <div className='Register'>
      <Top title={""}></Top>
      <Form
        className="Register-form"
        name="register"
        onFinish={onFinish}
        scrollToFirstError
      >
        <Form.Item
          name="name"
          label=""
          rules={[{ required: true, message: '!', whitespace: true }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          name="email"
          label="E-mail"
          rules={[
            {
              type: 'email',
              message: 'E-mail!',
            },
            {
              required: true,
              message: 'E-mail!',
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          name="password"
          label=""
          rules={[
            {
              required: true,
              message: '!',
            },
          ]}
          hasFeedback
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          name="confirm"
          label=""
          dependencies={['password']}
          hasFeedback
          rules={[
            {
              required: true,
              message: '!',
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue('password') === value) {
                  return Promise.resolve();
                }
                return Promise.reject(new Error('!'));
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item
          name={undefined}
          label=""
        >
          <ImgCrop rotate>
            <Upload
              name="avatar"
              action={'/upload'}
              listType="picture-card"
              fileList={fileList}
              onChange={onUpload}
              onPreview={onPreview}
            >
              {fileList.length < 1 && '+ Upload'}
            </Upload>
          </ImgCrop>

        </Form.Item>
        <Form.Item>
          <Button className='Register-submit' type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default Register;
