import axios from "axios"
import { Link, useLocation, useNavigate } from "react-router-dom"
import Top from "../../components/Top/Top"
import { Button, Checkbox, Form, Input } from 'antd';
import React from 'react';
import { LockOutlined, UserOutlined } from "@ant-design/icons";
import './Login.css';
import { useLocalstorage } from "rooks";
import LoginCheck from "../../components/LoginCheck";


const Login: React.FC = () => {
  const navigate = useNavigate()
  function loginSubmit(loginInfo) {
    return axios.post('/account/login', loginInfo).then(()=> {
      console.log('-1')
    }, ()=> alert('!'))
  }

  const onFinish = async (values: any) => {
    const loginInfo = {
      name: values.username,
      password: values.password,
    }
    console.log(loginInfo, 'logininfo')
    await loginSubmit(loginInfo)
    navigate(sessionStorage.voteLastPage, {replace: true})
    console.log('Success:', values, sessionStorage.voteLastPage);
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <div className='Login'>
      <Top title={""}></Top>
      <Form
        name="normal_login"
        className="login-form"
        initialValues={{ remember: true }}
        onFinish={onFinish}>
        <Form.Item
          name="username"
          rules={[{ required: true, message: ''}]}>
          <Input size="large"
            className="Login-userName"
            prefix={<UserOutlined className="site-form-item-icon" />} placeholder="" />
        </Form.Item>
        <Form.Item
          name="password"
          rules={[{ required: true,message: '' }]}>
          <Input
            size="large"
            className="Login-passWord"
            prefix={<LockOutlined className="site-form-item-icon" />}
            type="password"
            placeholder=""/>
        </Form.Item>
        <Form.Item className="Login-remember">
          <Form.Item name="remember" valuePropName="checked" noStyle>
            <Checkbox ></Checkbox>
          </Form.Item>
          <Link to="/register"></Link>
        </Form.Item>

        <Form.Item>
          <Button size="large" type="primary" htmlType="submit" className="Login-submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default LoginCheck(Login, {nav: '/'});

